@use 'base/variables' as v;
@use 'base/mixins' as m;

.notavif.notwebp .cita {
    background-image: url(../img/bg_citas.jpg);
}

.webp .cita {
    background-image: url(../img/bg_citas.webp);
}

.avif .cita {
    background-image: url(../img/bg_citas.avif);
}

.cita {
    background-repeat: no-repeat;
    background-size: cover;
    background-position:  center ;
    background-attachment: fixed;
    padding: 5rem 0;
    color: v.$blanco;
    text-align: center;

    &__heading {
        @include m.heading;
        font-size: 6rem;
    }

    &__texto {
        text-align: center;
        max-width: 50rem;
        margin: 0 auto;
        line-height: 1.8;
    }

    &__link {
        @include m.boton(v.$primario, v.$blanco);
        margin-top: 4rem;
        padding: 2rem 10rem;

    }
}