@use 'base/variables' as v;
@use 'base/mixins' as m;

.productos {
    &__heading {
        @include m.headingCursiva;
        margin-bottom: 5rem;
    }

    &__grid {
        @include m.grid(1, 2rem);

        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop {
            @include m.grid(4, 2rem);
        }
    }

}

.producto {

    background-color: v.$blanco;
    
    &__imagen-mask {
        overflow: hidden;
    }

    &__imagen {
        width: 100%;
        transition: transform 400ms ease-in-out;
        &:hover {
            transform: scale(1.2);
         }
    }

    &__heading {
        @include m.heading;
        font-size: 2.4rem;
        color: v.$secundario;
    }

    &__texto {
        line-height: 1.8;
        text-transform: uppercase;
        text-align: center;
        font-size: 1.6rem;
        padding: 0 2rem;
    }

    &__precio {
        text-align: center;
        font-size: 2.4rem;
        color: v.$gris;
    }
}