@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    text-align: center;
    padding-top: 5rem;
    &__grid {
        @include m.tablet {
            @include m.grid(3, 2rem);
        }
    }

    &__box {
        :last-of-type {
            display: flex;
            justify-content: center;
        }
    }

    &__heading {
        @include m.heading;
        font-size: 3.6rem;
    }

    &__texto {
        line-height: 1.8;
    }

    &__copyright {
        color: v.$gris;
        font-size: 1.6rem;
        border-top: 1px solid darken(v.$grisClaro, 30%);
        margin: 2rem auto 0 auto;
        padding: 2rem;
        max-width: 120rem;
    }
}