@use 'base/mixins' as m;
@use 'base/variables' as v;

.sociales {
    margin: 5rem 0;
    display: flex;
    
    @include m.tablet {
        margin: 0;
    }
    &__link {
        margin-right: .5rem;
        
        &[href*="facebook.com"]::before {
            content: "";
            display: inline-block;
            background-image: url(../img/icono_facebook.svg);
            padding: 2rem;
        }
        
        &[href*="twitter.com"]::before {
            content: "";
            display: inline-block;
            background-image: url(../img/icono_twitter.svg);
            padding: 2rem;
        }
        
        &[href*="instagram.com"]::before {
            content: "";
            display: inline-block;
            background-image: url(../img/icono_instagram.svg);
            padding: 2rem;
        }
        
        &[href*="pinterest.com"]::before {
            content: "";
            display: inline-block;
            background-image: url(../img/icono_pinterest.svg);
            padding: 2rem;
        }
        
        &[href*="youtube.com"]::before {
            content: "";
            display: inline-block;
            background-image: url(../img/icono_youtube.svg);
            padding: 2rem;
        }
        
        &[href*="tiktok.com"]::before {
            content: "";
            display: inline-block;
            background-image: url(../img/icono_tiktok.svg);
            padding: 2rem;
        }
    }
    &__accesible {
        clip: rect(0,0,0,0);
        border: 0;
        display: block;
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        width: 1px;
    }
}