@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    background-color: v.$primario;
    padding: .3rem 0;
    margin-block: 2rem;
    
    &__contenedor {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }
    
    &__border {
        padding: 1.7rem 0;
        border-block: 2px dashed v.$blanco;;
    }

    &__enlace {
        color: v.$blanco;
        font-size: 2.4rem;

        margin-bottom: 1.2rem;
        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet {
            margin: 0;
        }
    }
}