@use 'base/variables' as v;
@use 'base/mixins' as m;


.notavif.notwebp .horario {
    background-image: linear-gradient(to bottom, v.$primario 0%, v.$primario 50%, transparent 50%),
        url(../img/bg_horarios.jpg);

    @include m.tablet {
        background-image: linear-gradient(to right, v.$primario 0%, v.$primario 50%, transparent 50%),
            url(../img/bg_horarios.jpg);
    }
}

.webp .horario {
    background-image: linear-gradient(to bottom, v.$primario 0%, v.$primario 50%, transparent 50%),
        url(../img/bg_horarios.webp);

    @include m.tablet {
        background-image: linear-gradient(to right, v.$primario 0%, v.$primario 50%, transparent 50%),
            url(../img/bg_horarios.webp);
    }
}

.avif .horario {
    background-image: linear-gradient(to bottom, v.$primario 0%, v.$primario 50%, transparent 50%),
        url(../img/bg_horarios.avif);

    @include m.tablet {
        background-image: linear-gradient(to right, v.$primario 0%, v.$primario 50%, transparent 50%),
            url(../img/bg_horarios.avif);
    }
}

.horario {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center,  71%;
    
    &__grid {
        display: grid;
        grid-template-rows: repeat(2, 1fr);

        @include m.tablet {
            grid-template-rows: unset;
            @include m.grid(2, 4rem);
        }
    }

    &__contenido {
        padding: 5rem 0;
        color: v.$blanco;
    }

    &__heading {
        @include m.heading;
    }

    &__texto {
        line-height: 1.8;
        text-align: center;
    }
}

.table {
    width: 100%;
    border-collapse: collapse;
    border: .5rem v.$secundario solid;
    
    &__thead {
        background-color: v.$secundario;
    }

    &__heading {
        padding: 2rem;
    }

    &__row {
        &:hover {
            background-color: darken(v.$primario, 10%);
        }
    }

    &__data {
        padding: 1rem;
        text-align: center;
        font-size: 2.4rem;
    }
}